@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&family=Quicksand:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.title
{
  font-family: "Quicksand";
  color: #0C0C0C;
}

.text
{
  font-family: "Poppins";
  color: #0C0C0C;
}

.color-white
{
  color: #f9f9f9;
}
.App
{
  background-color: #D0E8F9;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  display: flex;
  flex-direction: row;
}

.error-msg
{
  color: red;
  padding: 8px;
  text-align: center;
}

@media (max-width:900px) 
{
  .App
  {
    background-color: #D0E8F9;
    width: 100vw;
    min-height: 100vh;
    height: auto;
    overflow-x: hidden;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
  }

}
.sidebar
{
    min-width: 20vw;
    max-width: 20vw;
    height: 100vh;
    background-color: #FAFAFA;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo-holder
{
    width: 40%;
    margin-top: 32px;
}

.footer, .logo-holder img:hover
{
    cursor: pointer;
}

.logo
{
    width: 100%;
}


a
{
    text-decoration: none;
}

.footer
{
    position: absolute;
    bottom: 0;
}

@media (max-width:900px) 
{
    .sidebar
    {
        min-width: 100vw;
        height: 100px;
        background-color: #FAFAFA;
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .logo-holder
    {
        height: 80px;
        width: 80px;
        margin: 0 16px;
    }

    .logo
    {
        height: 80px;
        width: 80px;
    }

    .footer
    {
        position: relative;
        width: 100%;
        text-align: end;
        margin-right: 16px;
    }
}
.container
{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding-top: 32px;
}

.title
{
    text-align: center;
}
.modal-container
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #00000095;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.modal-container-options
{
    padding: 16px;
    background-color: #f9f9f9;
    border-radius: 5px;
    margin-left: 20px;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.modal-container-btns-row
{
    width: 70%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 32px;
}


@media (max-width: 900px) 
{
 
    .modal-container-btns-row
    {
        width: 100%;
        
    }
}


.row
{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.row img
{
    width: 25vw;
    margin-right: 5vw;
}


@media (max-width: 900px) 
{

    .row
{
    display: flex;
    flex-direction: row;
    flex-direction: column-reverse;
    align-items: center;
    flex-wrap: wrap; 
}

    .row img
    {
        width: 60vw;
        margin-top: -30px;
    }

}

.options-container
{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}



.options-btn
{
    width: 100%;
    height: 54px;
    font-family: "Poppins";
    font-size: 20px;
    align-self: center;
    font-family: "Poppins" !important;

}

.options-btn-right
{
    background-color: green !important;
    color: white !important;
}

.options-btn-wrong
{
    background-color: red !important;
    color: white !important;
}

.disabled-button
{
    pointer-events: none !important;    
}

@media (max-width: 900px) 
{
    .title
    {
        margin-left: 0;
        margin-right: 0;
    }

    .options-btn
    {
        width: 100%;
    }
}
.quiz-container
{
    width: 100%;
    margin-right: 10%;
    margin-left: 10%;
    flex-direction: column;
    align-items: center;
}

.questions-mark-row
{
    width: 100%;
    display: flex;
    align-self: center;
    flex-flow: row;
    justify-content: center;
    flex-wrap: wrap;
}

.buttons-row
{
    display: flex;
    flex-direction: row;
    margin-top: 32px;
    margin-bottom: 16px;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap;
}

.question-body
{
    width: 100%;
    justify-content: center;
}

.question-mark-circle
{
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    align-self: center;
    margin: 12px;
    border-radius:  50%;
    border-color: black;
    border-width: 1px;
    border-style: solid;
    font-family: "Poppins";
    color: black;
    align-self: center;
}

.questions-information
{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.question-mark-current
{
    border-color: #1f4105;
    background-color: #3f51b5;
    color: white;
}

.question-mark-right
{
    border-color: darkgreen;
    background-color: green;
    color: white;
}

.question-mark-wrong
{
    border-color: darkred;
    background-color: red;
    color: white;
}

@media (max-width: 900px) 
{
    .quiz-container
    {
        margin-right: 3%;
        margin-left: 3%;
        width: 94%;
    }
    
}
.result-modal-container
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #00000095;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.result-modal-container-options
{
    height: 80vh;
    min-height: 20vh;
    width: 50vw;
    background-color: #f9f9f9;
    padding: 16px;
    border-radius: 5px;
    margin-left: 20px;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
}

.results-cards-display-container
{
    max-width:50vw;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    height: 500%;
}

.right-card
{
    background-color: #a0f0a080;
    border-radius: 8px;
    width: 100%;
    margin-top: 8px;
    align-self: center;   
}

.wrong-card
{
    background-color: #f1616880;
    border-radius: 8px;
    width: 100%;
    margin-top: 8px;
    align-self: center;
}

.card
{
    padding: 8px;
}

.card-row
{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.right-option
{
    background-color: darkgreen;
    color: white;
    padding: 8px;
    border-radius: 4px;
}

.wrong-option
{
    background-color: red;
    color: white;
    padding: 8px;
    border-radius: 4px;
}

@media (max-width:900px) 
{

    .result-modal-container-options
    {
    width: 90vw;
    }

    .results-cards-display-container
    {
        max-width:90vw;
    }    
}
.results-container
{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 32px;
    align-items: center;
    overflow: auto;
}

.results-column
{
    width: 100%;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
}

@media (max-width:900px) {
    
    .results-container
    {
        width: 90%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0;
        margin-left: 5%;
        padding-top: 32px;
    }
}
